import {
  Component,
  EventEmitter,
  Input, OnChanges, OnInit,
  Output, SimpleChanges, ChangeDetectorRef, OnDestroy,
} from '@angular/core';
import {InputBoolean, NzI18nService} from "ng-zorro-antd";
import {MoveableListItem} from "../cmacs-moveable-list/cmacs-moveable-list.component";
import {CookieService} from "ngx-cookie-service";
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
  selector: 'cmacs-grid-configuration-modal',
  exportAs: 'cmacsGridConfigurationModal',
  templateUrl: './cmacs-grid-configuration-modal.component.html',
  styleUrls: ['./cmacs-grid-configuration-modal.component.css']
})
export class CmacsGridConfigurationModalComponent implements OnInit, OnChanges, OnDestroy {

  @Input() @InputBoolean() visible = false;
  @Input() modalTitle: string = '';
  @Input() header: string = '';
  @Input() saveBtnLabel: string = '';
  @Input() cmacsStyle = {};
  @Input() gridID: string;
  @Input() data: any;
  @Output() visibleChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() dataChange: EventEmitter<any> = new EventEmitter<any>();

  origin = null;
  private destroy$ = new Subject();

  constructor(private cookies: CookieService, private cdr: ChangeDetectorRef, private i18n: NzI18nService) { }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  ngOnInit() {
    this.origin = JSON.parse(JSON.stringify(this.data));
    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      switch (this.i18n.getLocale().locale) {
        case 'de':
          this.saveBtnLabel = 'In Cookies speichern';
          this.modalTitle = 'Spaltenoptionen';
          this.header = 'Spaltenoptionen';
          break;
        case 'en':
          this.saveBtnLabel = 'Save to Cookies';
          this.modalTitle = 'Column Options';
          this.header = 'Column Options';
          break;
        default:
          this.saveBtnLabel = 'Save to Cookies';
          this.modalTitle = 'Column Options';
      }
      this.cdr.markForCheck();
    });
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.visible && changes.visible.currentValue) {
      this.origin = JSON.parse(JSON.stringify(this.data));
    }
  }

  onDataChange($event: MoveableListItem[]) {
    this.data.fields = $event;
    this.dataChange.emit(JSON.parse(JSON.stringify(this.data)));
  }

  saveConfig() {
    this.visible = false;
    this.visibleChange.emit(false);

    this.cookies.set(this.gridID, JSON.stringify(this.data), 365);
  }

  onVisibleChange($event) {
    this.dataChange.emit(this.origin);
    this.visibleChange.emit($event);
  }

}
